.slider{
    display: flex;
    flex-direction: column;
    position: relative;
    background-color: #fff;
    margin: 0 auto;
    @include media(md){
        margin: 0;
    }

    &__item{
        min-width: 225px;
        &:first-child{
            @include media(md){
                margin-top: 51px;
            }
        }
        &:last-child{
            @include media(md){
                margin-bottom: 0;
            }
        }
        @include media(md) {
            display: flex;
            flex-direction: row;
            align-items: center;
            margin: 0 0 34px 30px;
        }

    }
    &__number{
        width: 66px;
        height: 66px;
        border-radius: 50%;
        -webkit-border-radius: 50%;
        -moz-border-radius: 50%;
        -ms-border-radius: 50%;
        -o-border-radius: 50%;
        background-color: $accentColor;
        display: flex;
        justify-content: center;
        align-items: center;
        @include text(30px, 700, 44px, $primaryColor);
        margin: 52px auto 18px;
        @include media(md) {
            width: 61px;
            height: 61px;
            @include text(35px, 700, 51px, $primaryColor);
            margin: 0 18px 0 0;
        }
    }
    &__question{
        @include text(16px, 500, 23px, $textColor);
        text-align: center;
        margin-bottom:5px; 
        @include media(md){
            @include text(18px, 500, 27px, $textColor);
            text-align: start;
            margin-bottom:12px;
        }
    }
    &__answer{
        @include text(14px, 400, 20px, $textColor);
        text-align: center;
        margin-bottom: 12px;
        @include media(md){
            @include text(18px, 400, 26px, $textColor);
            text-align: start;
            margin: 0;
        }
    }
}

