.slider .slick-arrow{
    position: absolute;
    top: 37%;
    z-index: 10;
    font-size: 0;
    opacity: inherit;
    width: 10px;
    height: 30px;
    @include media(md) {
        display: none;
    }

}
.slider .slick-arrow.slick-prev{
    left: 1px;
    background-image: url(./../images/slider-arrow-l.png);
    background-size: 100%;
    background-repeat: no-repeat;
    background-color: $primaryColor;
    border: none;
    @include media(md) {
        display: none;
    }
}
.slider .slick-arrow.slick-next{
    right: 1px;
    background-image: url(./../images/slider-arrow-r.png);
    background-size: 100%;
    background-repeat: no-repeat;
    background-color: $primaryColor;
    border:none;
    @include media(md) {
        display: none;
    }
}

.slider .slick-dots{
    display: flex;
    justify-content: center;
    align-items: center;
    @include media(md) {
        display: none;
    }
}
.slider .slick-dots li{
    list-style: none;
    margin: 0 6px;
    @include media(md) {
        display: none;
    }
}
.slider .slick-dots li button{
    font-size: 0;
    width: 10px;
    height: 10px;
    background-color:transparent;
    border: 0.5px solid $textColorLight;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    @include media(md) {
        display: none;
    }
}

.slider .slick-dots li.slick-active button {
    background-color:$accentColor;
}

#slick-slide-control00{
    display: none;
